'use client';

import { useState, useEffect } from 'react';
import styles from '../User/PageStyle.css';

interface Train {
  id: number;
  name: string;
  departure: string;
  destination: string;
  time: string;
}

export default function UserPage() {
  const [trains, setTrains] = useState<Train[]>([]);
  const [searchTerm, setSearchTerm] = useState('');
  const [sortColumn, setSortColumn] = useState<keyof Train | null>(null);
  const [sortDirection, setSortDirection] = useState<'asc' | 'desc'>('asc');
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  // Завантаження даних з API
  useEffect(() => {
    const fetchTrains = async () => {
      try {
        const response = await fetch('http://localhost:3009/trains');
        if (!response.ok) {
          throw new Error('Не вдалося завантажити дані');
        }
        const data: Train[] = await response.json();
        setTrains(data);
        setLoading(false);
      } catch (err) {
        setError('Помилка при завантаженні даних');
        setLoading(false);
      }
    };

    fetchTrains();
  }, []);

  // Функція для сортування
  const handleSort = (column: keyof Train) => {
    const newDirection = sortColumn === column && sortDirection === 'asc' ? 'desc' : 'asc';
    setSortColumn(column);
    setSortDirection(newDirection);

    const sortedTrains = [...trains].sort((a, b) => {
      const valueA = a[column];
      const valueB = b[column];
      if (newDirection === 'asc') {
        return valueA > valueB ? 1 : -1;
      } else {
        return valueA < valueB ? 1 : -1;
      }
    });
    setTrains(sortedTrains);
  };

  // Функція для пошуку
  const filteredTrains = trains.filter((train) =>
    train.name.toLowerCase().includes(searchTerm.toLowerCase())
  );

  if (loading) {
    return <div className={styles.container}>Завантаження...</div>;
  }

  if (error) {
    return <div className={styles.container}>{error}</div>;
  }

  return (
    <div className={styles.container}>
      <h1 className={styles.title}>Rozklad poyizdiv</h1>

      <div className={styles.searchWrapper}>
        <input
          type="text"
          placeholder="Poshuk za nazvoyu poyizda..."
          value={searchTerm}
          onChange={(e) => setSearchTerm(e.target.value)}
          className={styles.searchInput}
        />
      </div>

      <table className={styles.trainTable}>
        <thead>
          <tr className={styles.tableHeader}>
            <th className={styles.tableHeaderCell} onClick={() => handleSort('id')}>
              ID {sortColumn === 'id' && (sortDirection === 'asc' ? '↑' : '↓')}
            </th>
            <th className={styles.tableHeaderCell} onClick={() => handleSort('name')}>
              Nazva {sortColumn === 'name' && (sortDirection === 'asc' ? '↑' : '↓')}
            </th>
            <th className={styles.tableHeaderCell} onClick={() => handleSort('departure')}>
              Vidpravlennya {sortColumn === 'departure' && (sortDirection === 'asc' ? '↑' : '↓')}
            </th>
            <th className={styles.tableHeaderCell} onClick={() => handleSort('destination')}>
              Pryznachennya {sortColumn === 'destination' && (sortDirection === 'asc' ? '↑' : '↓')}
            </th>
            <th className={styles.tableHeaderCell} onClick={() => handleSort('time')}>
              Chas {sortColumn === 'time' && (sortDirection === 'asc' ? '↑' : '↓')}
            </th>
          </tr>
        </thead>
        <tbody>
          {filteredTrains.map((train) => (
            <tr key={train.id} className={styles.tableRow}>
              <td className={styles.tableCell}>{train.id}</td>
              <td className={styles.tableCell}>{train.name}</td>
              <td className={styles.tableCell}>{train.departure}</td>
              <td className={styles.tableCell}>{train.destination}</td>
              <td className={styles.tableCell}>{train.time}</td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
}